.exitConteiner {
  color: #666666;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  border-radius: 10px 10px 10px 10px;
  position: inherit;
}
.exit-befor {
  margin-top: 0;
  font-size: 20px;
  margin-left: 3px;
  margin-bottom: 0;
}
.exitInput {
  outline: none;
  resize: none;
  overflow: hidden;
  color: #666666;
  font-size: 18px;
  background-color: #fafafa21;
  border-radius: 5px;
  height: 4rem;
}
.exit-after {
  font-size: 20px;
  margin-top: 0;
  margin-left: 3px;
}
